.cart {
  grid-area: cart;
  grid-column: 12;
  grid-row: 1;
  justify-self: flex-end;
  max-width: 80px;
  margin-right: -10px;
}

.cart__link {
  display: block;
  padding: 10px;
  font-family: $font-pop;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: $color-black;
  text-decoration: none;
  text-align: center;
  transition: 0.5s;

  &:hover,
  &:focus {
    color: $color-brown;
  }
}

.cart__icon {
  display: none;
}

@media (max-width: $tablet-width) {
  .cart {
    grid-column: 4;
    align-self: center;
    margin-right: -10px;
  }

  .cart__link {
    display: flex;
  }

  .cart__item-name {
    display: none;
  }

  .cart__counter {
    display: none;
  }

  .cart__icon {
    display: block;
    fill: transparent;
    stroke: #ffffff;
    transition: 0.5s;

    &:hover,
    &:focus {
      transform: scale(1.2);
    }
  }

  .cart__icon--color {
    stroke: $color-black;
  }
}
